.hero {
	// padding-bottom: 5px
	position: relative;
	&__title {
		position: absolute;
		top: 21%;
		left: 24%;
		// right: 30%
		font-size: 35px;
		line-height: 55px;
		text-transform: uppercase;
		color: $main-color;
		width: 100%;
		transform: translate(0%, -17%); }
	&__subtitle {
		position: absolute;
		top: 33%;
		left: 24%;
		// right: 49%
		font-size: 35px;
		line-height: 55px;
		color: #fffffe;
		width: 100%;
		transform: translate(0%, -17%); }
	&__description {
		position: absolute;
		top: 44%;
		// right: 20%
		left: 24%;
		opacity: 0.42;
		color: #fffffe;
		font-size: 20px;
		line-height: 28px;
		width: 10%;
		transform: translate(0%, -17%); }
	&__button {
		position: absolute;
		top: 60%;
		left: 24%;
		padding: 15px 38px;
		outline: none;
		border: none;
		@include bold;
		transform: translate(0%, -17%);
		font-size: 18px;
		text-transform: uppercase; } }
.dots {
	&__list {
		position: absolute;
		top: 92%;
		left: 46%;
		transform: translate(-50%, -50%); }
	&__item {
		width: 13px;
		height: 13px;
		background-color: $white;
		margin-left: 15px;
		list-style: none;
		display: inline-block;
		&:hover {
			cursor: pointer; } } }
.active {
	display: none;
	background-color: $main-color;
	width: 13px;
	height: 13px;
	display: inline-block; }

@media (max-width: 1366px) {
	.hero {
		&__title {
			font-size: 30px;
			line-height: 50px; }
		&__subtitle {
			font-size: 30px;
			line-height: 50px; }
		&__description {
			font-size: 17px;
			line-height: 24px; }
		&__button {
			font-size: 16px; } } }
@media (max-width: 1200px) {
	.hero {
		&__title {
			font-size: 25px;
			line-height: 40px; }
		&__subtitle {
			font-size: 25px;
			line-height: 40px; }
		&__description {
			font-size: 15px;
			line-height: 20px; }
		&__button {
			font-size: 16px; } } }
@media (max-width: 992px) {
	.hero {
		&__title {
			font-size: 20px;
			line-height: 30px; }
		&__subtitle {
			font-size: 20px;
			line-height: 30px; }
		&__description {
			font-size: 13px;
			line-height: 17px; }
		&__button {
			font-size: 14px; } } }
@media (max-width: 984px) {
	.hero {
		&__button {
			background-color: $main-color; } } }
@media (max-width: 768px) {
	.hero {
		&__title {
			font-size: 16px; }
		&__subtitle {
			font-size: 16px;
			line-height: 22px; }
		&__description {
			font-size: 12px;
			line-height: 16px;
			width: 15%; }
		&__button {
			font-size: 14px;
			background-color: $main-color; } }
	.dots {
		&__list {
			top: 90%; } }
	.slider__image {
		background-position: cover; } }
@media (max-width: 576px) {
	.hero {
		&__title {
			font-size: 13px;
			line-height: 20px; }
		&__subtitle {
			font-size: 14px;
			line-height: 20px; }
		&__description {
			font-size: 10px;
			line-height: 15px;
			width: 15%; }
		&__button {
			font-size: 12px;
			top: 67%;
			background-color: $main-color; } }
	.dots {
		&__list {
			top: 88%;
			left: 48%; } } }
@media (max-width: 320px) {
	.hero {
		&__title, &__subtitle, &__description, &__button {
			display: none; } }
	.dots {
		&__list {
			top: 79%;
			left: 45%; } } }





