.comments {
	background-color: $black;
	width: 100%;
	position: relative;
	margin-bottom: 150px;
	padding: 110px;
	&__text {
		width: 470px;
		color: $white;
		font-size: 20px;
		line-height: 28px; }
	&__name {
		color: $main-color;
		font-size: 20px;
		line-height: 28px; }
	&-block {
		display: flex;
		justify-content: space-between;
		text-align: center;
		&__item {
			width: 50%; } }
	&__title {
		// position: absolute
		// top: -7%
		// left: 4%
		background-color: $main-color;
		text-align: center;
		text-transform: uppercase;
		font-size: 28px;
		line-height: 28px;
		padding: 19px 65px;
		@include bold;
		margin-top: -145px;
		margin-left: -50px;
		margin-bottom: 55px;
		display: block;
		max-width: 255px; }
	&-girl {
		&__commas {
			position: absolute;
			top: 19%;
			left: 31%; } }
	&-man {
		&__commas {
			position: absolute;
			top: 19%;
			right: 23%; } }
	&__counts {
		margin-top: 25px; }
	&__count {
		color: $white;
		margin-right: 10px; } }
@media (max-width: 1200px) {
	.container {
		width: 90%; }
	.comments {
		&__title {
			margin-left: -142px; } } }
@media (max-width: 992px) {
	.comments {
		&__title {
			font-size: 24px; }
		&__name {
			font-size: 18px; }
		&__text {
			font-size: 16px; }
		&-block {
			&__item {
				width: 46%; } } } }
@media (max-width: 768px) {
	.comments {
		&-block {
			&__item {
				width: 49%;
				margin-left: -30px;
				margin-right: -30px; } } } }
@media (max-width: 576px) {
	.comments {
		&-block {
			flex-direction: column;
			margin-left: 65px;
			&__item {
				width: 100%;
				margin-top: 26px; } }
		&__text {
			font-size: 13px; } } }
@media (max-width: 320px) {
	.comments {
		&-block {
			margin-left: 0px;
			&__item {
				width: 304px;
				margin-left: -105px; } }
		&__text {
			font-size: 12px; }
		&__title {
			font-size: 18px;
			margin-left: -17px; }
		&__title a {
			margin-left: -35px; }
		&__counts {
			width: 318px; } } }
