.modal-footer {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(#000, 0.5);
	display: none;
	&_active {
		display: flex; }
	&-dialogf {
		position: relative;
		margin: auto;
		max-width: 450px;
		width: 90%;
		min-width: 200px;
		background-color: #fff;
		padding: 40px;
		&__title-f {
			text-align: center; }
		&__close-f {
			position: absolute;
			top: 0;
			right: 0;
			border: none;
			background: transparent;
			outline: none;
			cursor: pointer; }
		&__input-f {
			width: 100%;
			padding: 20px;
			border-radius: 10px;
			outline: none;
			font-size: 20px;
			border: 1px solid #ddd {
    		padding: 20px 26px;
    		margin: 0 auto; } } } }
.modal-dialog__btn-f {
    	padding: 18px 35px;
    	font-weight: 700;
    	font-size: 13px;
    	@include bold;
    	margin-top: 20px;
    	width: 100%; }
@media (max-width: 992px) {
	.modal-dialog__input {
		font-size: 14px; } }
@media (max-width: 576px) {
	.modal-dialog__btn {
		background-color: $main-color;
		border: none; } }
@media (max-width: 320px) {
	.modal-dialog {
		&__title {
			font-size: 13px; } }
	.modal-dialog__input {
		font-size: 11px; } }
