.offer {
	margin-top: 100px;
	// margin-bottom: 80px
	background-color: $bg;
	width: 100%;
	// opacity: 0.55
	position: relative;
	&-block {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-top: 105px;
		padding-bottom: 120px;
		margin-bottom: 150px; }
	&__item {
		margin-top: 21px;
		position: relative;
		z-index: 99;
		color: $white;
		&:hover {
			color: $black;
			@include bold; } }
	&__image {
		filter: brightness(30%);
		&:hover {
			filter: brightness(80%);
			transition: 0.3s; } }
	&__wrapper {
		position: absolute;
		top: 18%;
		left: 18%;
		width: 257px;
		font-size: 20px;
		line-height: 26px; }
	&__title {
		text-align: center;
		// position: absolute
		// top: 57%
		// left: 33%
		// background-color: $black
		// padding: 11px 20px
		// font-size: 15px
		// text-transform: uppercase
		// +bold
		// text-align: center
		// width: 38%
		// display: none
		// &:hover
 }		// 	display: block
	&__button {
		background-color: $main-color;
		padding: 11px 20px;
		font-size: 15px;
		text-transform: uppercase;
		@include bold;
		position: absolute;
		top: 57%;
		left: 33%;
		color: $black;
		&:hover {
			// background-color: $black
			display: none;
			// color: $white
			cursor: pointer; } }
	&__hover {
		display: none;
		&:hover {
			color: $white;
			display: block; } } }
.section__title {
	// position: absolute
	// top: -4%
	// left: 4%
	margin-left: -45px;
	background-color: $main-color;
	text-align: center;
	text-transform: uppercase;
	font-size: 28px;
	line-height: 28px;
	padding: 19px 65px;
	@include bold; }
@media (max-width: 1200px) {
	.offer img {
		width: 345px;
		height: 225px; } }
@media (max-width: 992px) {
	.offer {
		&__wrapper {
			left: 14%; }
		&__button {
			font-size: 14px; } }
	.section__title {
		font-size: 22px;
		line-height: 25px;
		margin-left: -28px; }
	.offer-block {
		margin-left: 60px;
		margin-right: 60px; } }
@media (max-width: 768px) {
	.offer img {
		width: 275px; }
	.offer {
		&__title {
			margin-left: -37px; }
		&__button {
			margin-left: -9px; } } }
@media (max-width: 576px) {
	.offer {
		&-block {
			margin-left: 141px; } }
	.section__title {
		margin-left: 53px; } }
@media (max-width: 320px) {
	.offer {
		&-block {
			width: 100%;
			margin-left: -1px; }
		&__title {
			font-size: 12px;
			margin-left: 32px;
			width: 150px; }
		&__button {
			font-size: 11px;
			margin-left: -7px; } }
	.section__title {
		margin-left: 39px; } }
